body {
  margin: 0;
  padding: 0;
  font-size: 10px;
  font-family: Roboto, 'Helvetica Neue', sans-serif;
}

html {
  height: 100%;
  font-size: 10px;
}

h1,
h2,
h3 {
  margin: 1.5rem 0 1rem;
  font-weight: 300;
}

h1 {
  margin: 1.5rem 0 1rem 1.5rem;
  font-size: 2.5rem;
}

h2 {
  font-size: 2rem;
}

h3 {
  font-size: 1.5rem;
}

app-root {
  display: block;
  height: 100%;
}

a {
  color: #3f51b5;
  font-size: 1.4rem;
}

.MuiTableHead-root-149 tr th {
  font-size: 1.2rem;
}

.MuiTableBody-root-167 td {
  font-size: 1.4rem;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.red {
  color: #ed1b24;
}

.green {
  color: #008000;
}

.blue {
  color: #3f51b5;
}

.base-margin {
  margin: 20px 0 0 0;
}

.db-controls button {
  font-size: 1.4rem;
  text-transform: capitalize;
}

.input-wrapp .input label {
  font-size: 1.4rem;
}

.input-wrapp .input > div {
  font-size: 1.4rem;
}

.input-wrapp .input {
  min-width: 300px;
}

h4.margin-bottom {
  margin-bottom: 2rem;
}

div[class*='MuiTooltip'] {
  font-size: 1.2rem;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.wrapper {
  position: relative;
  max-width: 120em;
  width: 100%;
  margin: 0 auto;
  padding: 0 1.5em;
  display: block;
  box-sizing: border-box;
}

button.change-panels-button {
  position: absolute;
  top: -1.5rem;
  right: 1.5rem;
  font-size: 1.4rem;
  text-transform: capitalize;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.edit-text {
  font-size: 1.4rem;
}

.input-name-width {
   width: 100%;
}

.link {
  width: 40vw;
  display: block;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.MuiTable-root-148 td,
.MuiTable-root-148 th {
  padding: 1.5rem;
}

.controls-padding span {
  padding: 0;
}

td .controls-padding {
  padding: 0;
}

.controls-padding label {
  margin: 0;
}

.base-margin th {
  font-size: 1.2rem;
}

.base-margin td {
  font-size: 1.4rem;
}

td.padding-td,
th.padding-td {
  padding: 0 0 0 15px;
}

.date {
  width: 7.5rem;
  display: block;
}

.new-competition-form .add-link {
  margin-top: 9px;
}

.error {
  font-size: 1.2rem;
  margin-top: 1rem;
}

.link-input {
  width: calc(100% - 10rem);
}

.form-width {
  width: 100%;
}

.db-controls button {
  margin: .9rem 1.5rem 0 0;
}

.db-controls button:last-child {
  margin: .9rem 0 0 0;
}

.buttons-wrapper {
  display: flex;
  justify-content: space-between;
}

.buttons-wrapper form {
  width: calc(100% - 40rem);
}

tr.invisible-row {
  display: none;
}

.input.input-name-width label,
.input.input-name-width div input {
  font-size: 1.4rem;
}

.select-gender select,
.select-gender select option{
  font-size: 1.4rem;
}

.runners-list {
  font-size: 1.4rem;
  margin-bottom: 1rem;
}

.trash-button {
  background: none;
  border: none;
  cursor: pointer;
}

.trash-button span {
  font-size: 2.4rem;
  margin-bottom: -5px;
  transition: -webkit-transform .2s ease-in-out;
  transition: transform .2s ease-in-out;
  transition: transform .2s ease-in-out, -webkit-transform .2s ease-in-out;
}

.trash-button span:hover {
  font-size: 2.4rem;
  margin-bottom: -5px;
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg);
}

.duplicates-runners {
  font-size: 1.2rem;
  margin-left: 3rem;
}

.base-margin.sticky-top {
  position: -webkit-sticky;
  position: sticky;
  top: 20px;
}

